<template>
  <div class="subject_edit">
    <head-top title="班级编辑">
      <span slot="right" @click="saveData">
           <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>
    <van-cell-group>
      <van-field required placeholder="班级名称" maxlength="50" v-model="className" :error-message="classNameErr"/>
    </van-cell-group>
  </div>
</template>

<script>
export default {
  name: "subject-edit",
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classID: this.$route.params.id || "",
      className: this.$route.params.name || "",
      classNameErr: ""
    };
  },
  methods: {
    saveData () {
      this.classNameErr = "";
      if (this.className === "") {
        this.classNameErr = "请输入班级名称";
        return;
      }
      this.$mainHttp.class.dataAdd({
        classID: this.classID,
        className: this.className,
        courseID: this.subjectID,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          if (this.$store.getters.class) {
            const classObj = this.$store.getters.class;
            classObj.name = this.className;
            this.$store.commit("SET_CLASS", classObj);
          }
          this.$router.go(-1);
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
